import React, {Component} from "react";
import classnames from "classnames";
import "../form.css"
import logo from "../../Images/1.jpeg"

export default class ForgetForm extends Component{
    constructor(){
        super();
        this.state = {
            email:"",
            errors:{}
        }
    }

    onSubmit = (e) => {
        e.preventDefault();
        var emreg=/^([A-Za-z0-9_\-\.\u4e00-\u9fa5])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,12})$/;
        try {
            const { data } = this.state;
            if (emreg.test(this.state.email) === true) {
                window.location = "/#/forget/verify";}
          } catch (e) {
            const { data } = this.state;
            if (emreg.test(this.state.email) === false) {
            window.location = "/"; 
            }
            }
        console.log(this.state);
        this.setState({errors:this.state})
    }
    changeHandle = (e) => {
        this.setState({
            [e.target.name]:e.target.value
        })
    }

    render(){
        const {email,errors} = this.state;
        return(
            <div className="form-horizontal">
                <form onSubmit={this.onSubmit}  action="/forget" method="POST">
                    <div className="logo-wrapper">
                        <img src={logo} className="logo" alt="背景图" />
                        <span className="title">Forget Password</span>
                    </div>
                    <div className="form-group">
                        <i class="bi bi-envelope-fill"></i>
                        <input
                            className={ classnames('form-control', {'is-invalid':errors.email===""})}
                            type="text"
                            name="email"
                            value={email}
                            onChange={this.changeHandle}
                            placeholder="email address"
                        />
                        { errors.email==="" ? 
                        <span style={{color:'red', fontSize:'10px'}}>Email address should not be empty.</span> : ''}
                    </div>
                    <div className="form-group">
                        <button class="btn btn-primary" type="submit" className="submitbutton">Send verification email</button>
                        <hr />
                    </div>
                </form>
            </div>
        )
    }
}
